import { Component, ViewEncapsulation, ChangeDetectionStrategy, ChangeDetectorRef, Optional } from '@angular/core';
import { matSortAnimations, MatSortHeader, MatSortHeaderIntl, ArrowViewStateTransition } from '@angular/material';
import { CdkColumnDef } from '@angular/cdk/table';
import { OMatSort } from './o-mat-sort';

@Component({
  moduleId: module.id,
  selector: '[o-mat-sort-header]',
  exportAs: 'oMatSortHeader',
  template: `
    <div class="mat-sort-header-container"
         [class.mat-sort-header-sorted]="_isSorted()"
         [class.mat-sort-header-position-before]="arrowPosition == 'before'">
      <button class="mat-sort-header-button" type="button"
              [attr.disabled]="_isDisabled() || null"
              [attr.aria-label]="_intl.sortButtonLabel(id)"
              (focus)="_setIndicatorHintVisible(true)"
              (blur)="_setIndicatorHintVisible(false)">
        <ng-content></ng-content>
      </button>

      <!-- Disable animations while a current animation is running -->
      <div class="mat-sort-header-arrow"
           [@arrowOpacity]="_getArrowViewState()"
           [@arrowPosition]="_getArrowViewState()"
           [@allowChildren]="_getArrowDirectionState()"
           (@arrowPosition.start)="_disableViewStateAnimation = true"
           (@arrowPosition.done)="_disableViewStateAnimation = false">
        <div class="mat-sort-header-stem"></div>
        <div class="mat-sort-header-indicator" [@indicator]="_getArrowDirectionState()">
          <div class="mat-sort-header-pointer-left" [@leftPointer]="_getArrowDirectionState()"></div>
          <div class="mat-sort-header-pointer-right" [@rightPointer]="_getArrowDirectionState()"></div>
          <div class="mat-sort-header-pointer-middle"></div>
        </div>
      </div>
    </div>
  `,
  styles: [`
    .mat-sort-header-container{display:flex;cursor:pointer}.mat-sort-header-disabled .mat-sort-header-container{cursor:default}.mat-sort-header-position-before{flex-direction:row-reverse}.mat-sort-header-button{border:none;background:0 0;display:flex;align-items:center;padding:0;cursor:inherit;outline:0;font:inherit;color:currentColor}.mat-sort-header-arrow{height:12px;width:12px;min-width:12px;margin:0 0 0 6px;position:relative;display:flex}.mat-sort-header-position-before .mat-sort-header-arrow{margin:0 6px 0 0}.mat-sort-header-stem{background:currentColor;height:10px;width:2px;margin:auto;display:flex;align-items:center}.mat-sort-header-indicator{width:100%;height:2px;display:flex;align-items:center;position:absolute;top:0;left:0}.mat-sort-header-pointer-middle{margin:auto;height:2px;width:2px;background:currentColor;transform:rotate(45deg)}.mat-sort-header-pointer-left,.mat-sort-header-pointer-right{background:currentColor;width:6px;height:2px;position:absolute;top:0}.mat-sort-header-pointer-left{transform-origin:right;left:0}.mat-sort-header-pointer-right{transform-origin:left;right:0}
  `],
  host: {
    '(click)': '_handleClick()',
    '(mouseenter)': '_setIndicatorHintVisible(true)',
    '(longpress)': '_setIndicatorHintVisible(true)',
    '(mouseleave)': '_setIndicatorHintVisible(false)',
    '[attr.aria-sort]': '_getAriaSortAttribute()',
    '[class.mat-sort-header-disabled]': '_isDisabled()',
  },
  encapsulation: ViewEncapsulation.None,
  changeDetection: ChangeDetectionStrategy.OnPush,
  inputs: ['disabled'],
  animations: [
    matSortAnimations.indicator,
    matSortAnimations.leftPointer,
    matSortAnimations.rightPointer,
    matSortAnimations.arrowOpacity,
    matSortAnimations.arrowPosition,
    matSortAnimations.allowChildren,
  ]
})
export class OMatSortHeader extends MatSortHeader {

  constructor(public _intl: MatSortHeaderIntl,
    changeDetectorRef: ChangeDetectorRef,
    @Optional() public _sort: OMatSort,
    @Optional() public _cdkColumnDef: CdkColumnDef) {

    super(_intl, changeDetectorRef, _sort, _cdkColumnDef);
  }

  _handleClick() {
    if (this._isDisabled()) { return; }

    this._sort.addSortColumn(this);

    // Do not show the animation if the header was already shown in the right position.
    if (this._viewState.toState === 'hint' || this._viewState.toState === 'active') {
      this._disableViewStateAnimation = true;
    }

    // If the arrow is now sorted, animate the arrow into place. Otherwise, animate it away into
    // the direction it is facing.
    const viewState: ArrowViewStateTransition = this._isSorted() ?
      { fromState: this._arrowDirection, toState: 'active' } :
      { fromState: 'active', toState: this._arrowDirection };
    this._setAnimationTransitionState(viewState);

    this._showIndicatorHint = false;
  }

  _isSorted() {
    return this._sort.isActive(this) && this._sort.hasDirection(this.id);
  }

  _updateArrowDirection() {
    this._arrowDirection = this._isSorted() ?
      this._sort.directionById[this.id] :
      (this.start || this._sort.start);
  }

  refresh() {
    if (this._sort.isActive(this)) {
      this._setAnimationTransitionState({
        fromState: this._sort.directionById[this.id],
        toState: 'active'
      });
      this._showIndicatorHint = false;
    } else {
      this._viewState.toState = 'active';
      this._intl.changes.next();
    }
  }
}
